import {Col, ListGroup} from 'react-bootstrap';
import React,{Component} from "react";
import axios from 'axios';
import { API_URL } from '../utils/constants';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import {faCow,faMugHot,faMugSaucer,faMartiniGlassCitrus, faPlateWheat, faCheese, faBowlFood} from '@fortawesome/free-solid-svg-icons';

const Icon = ({nama}) =>{
    if(nama === "Sumur") return <FontAwesomeIcon icon={faCow} className='mr-2'/>
    if(nama === "Coffee") return <FontAwesomeIcon icon={faMugHot} className='mr-2'/>
    if(nama === "Warkop") return <FontAwesomeIcon icon={faMugSaucer} className='mr-2'/>
    if(nama === "Signature") return <FontAwesomeIcon icon={faMartiniGlassCitrus} className='mr-2'/>
    if(nama === "Nasi Goreng") return <FontAwesomeIcon icon={faPlateWheat} className='mr-2'/>
    if(nama === "Cemilan") return <FontAwesomeIcon icon={faCheese} className='mr-2'/>
    if(nama === "Indomie") return <FontAwesomeIcon icon={faBowlFood} className='mr-2'/>

    return <FontAwesomeIcon icon={faCow} className='mr-2'/>
}
export default class ListCategories extends Component{
    constructor(props) {
      super(props)
    
      this.state = {
         categories: [],
      }
    }

    componentDidMount() {
        axios.get(API_URL+"categories")
          .then(res => {
            const categories = res.data;
            this.setState({ categories });
          })
          .catch(error =>{
            console.log(error)
          })
      }


    
    render(){
        const {categories}= this.state
        const {changeCategory, choosedCategory}= this.props
        return(
            <Col md={2} mt="2" style={{backgroundColor:'#FFE0AE',padding:'15px',borderRadius:'5px'}}>
                <h4><strong style={{color:'#5d3138'}}>Categories</strong></h4>
                <hr/>
            <ListGroup>
                {categories && categories.map((category)=>(

                <ListGroup.Item 
                key={category.id} 
                onClick={()=>changeCategory(category.nama)}
                className={choosedCategory === category.nama && "active-category"}
                style={{cursor:'pointer'}}
                >
                    <h5>
                    <Icon nama={category.nama}/> {category.nama}
                    </h5>
                    
                    
                </ListGroup.Item>


                ))}
            </ListGroup>
            </Col>
        )
    }
}