
import { Row, Col, Container } from 'react-bootstrap';
import ListCategories from '../components/ListCategories';
import Menus from '../components/Menus';
import Hasil from '../components/Hasil';
import React, { Component } from 'react'
import { API_URL } from '../utils/constants';
import axios from 'axios';
import swal from 'sweetalert';
export default class Home extends Component {

  constructor(props) {
    super(props)
  
    this.state = {
       menus: [],
       choosedCategory : 'Sumur',
       keranjangs: []
    }
  }

  componentDidMount() {
    axios.get(API_URL+"products?category.nama="+this.state.choosedCategory)
      .then(res => {
        const menus = res.data;
        this.setState({ menus });
      })
      .catch(error =>{
        console.log(error)
      });

     this.getListKeranjang();

  }
  // componentDidUpdate(prevState){
  //   if(this.state.keranjangs !== prevState.keranjangs){
  //     axios.get(API_URL+"keranjangs")
  //     .then(res => {
  //       const keranjangs = res.data;
  //       this.setState({ keranjangs });
  //     })
  //     .catch(error =>{
  //       console.log(error)
  //     });
  //   }
  // }

  getListKeranjang = () =>{
    axios.get(API_URL+"keranjangs")
    .then(res => {
      const keranjangs = res.data;
      this.setState({ keranjangs });
    })
    .catch(error =>{
      console.log(error)
    });

  }

  changeCategory = (value) =>{
    this.setState({
      choosedCategory: value,
      menus: []
    })

    axios.get(API_URL+"products?category.nama="+value)
      .then(res => {
        const menus = res.data;
        this.setState({ menus });
      })
      .catch(error =>{
        console.log(error)
      });
    
  }
  
  addKeranjang = (value) => {

    axios
    .get(API_URL + "keranjangs?product.id=" + value.id)
    .then((res) => {
      if (res.data.length === 0) {
        const keranjang = {
          jumlah: 1,
          total: value.harga,
          product: value,
        };

        axios
          .post(API_URL + "keranjangs", keranjang)
          .then((res) => {
            this.getListKeranjang();
            swal({
              title: "Added to Cart",
              text: "Added to Cart " + keranjang.product.nama,
              icon: "success",
              button: false,
              timer: 1500,
            });
          })
          .catch((error) => {
            console.log(error);
          });
      } else {
        const keranjang = {
          jumlah: res.data[0].jumlah + 1,
          total: res.data[0].total + value.harga,
          product: value,
        };

        axios
          .put(API_URL + "keranjangs/" + res.data[0].id, keranjang)
          .then((res) => {
            swal({
              title: "Added to Cart",
              text: "Added to Cart " + keranjang.product.nama,
              icon: "success",
              button: false,
              timer: 1500,
            });
          })
          .catch((error) => {
            console.log(error);
          });
      }
    })
    .catch((error) => {
      console.log(error);
    });
  }
  
  
  render() {
    const {menus, choosedCategory, keranjangs}= this.state
    return (
      <div>

      <div className='mt-3'>
        <Container fluid>
            <Row>
              <ListCategories changeCategory={this.changeCategory} choosedCategory={choosedCategory}/>
              <Col style={{padding:'15px'}}>
                <h4>
                  <strong style={{color:'#5d3138'}}>Products</strong>
                  <hr/>
                  <Row className='overflow-auto menu'>
                  {menus && menus.map((menu) => (
                  
                    <Menus
                      key={menu.id}
                      menu={menu}
                      addKeranjang={this.addKeranjang}
                    />

              
                    
                  ))}
                  </Row>
                </h4>
              </Col>
              <Hasil keranjangs={keranjangs}{...this.props}getListKeranjang={this.getListKeranjang}/>

            </Row>

        </Container>

      </div>
      
    </div>
    )
  }
}



