import Container from 'react-bootstrap/Container';
import Navbar from 'react-bootstrap/Navbar';

function NavbarComponents() {
  return (
    <Navbar variant="dark" expand="lg">
      <Container>

        <Navbar.Brand href="#home" style={{margin:'auto'}}> <img alt='campsurvivor' className='logo-camp' src='assets/images/logo/logo.png' style={{width:'200px',opacity: '100%'} }/> </Navbar.Brand>
      </Container>
    </Navbar>
  );
}

export default NavbarComponents;