import React, { Component } from 'react'
import { Button, Col, Row } from 'react-bootstrap'
import { numberWithCommas } from '../utils/utils';
import { Link } from 'react-router-dom'
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faShoppingBasket } from '@fortawesome/free-solid-svg-icons';
import axios from 'axios';
import {API_URL} from '../utils/constants'


export default class TotalPay extends Component {
   
    submitTotalPay = (totalPay) => {
        const pesanan = {
            total_pay: totalPay,
            menus: this.props.keranjangs
        }
       
           axios.post(API_URL+"pesanans", pesanan).then((res) => {
            this.props.history.push('/success')
        }) 
       
    };
  render() {
const totalPay = this.props.keranjangs.reduce(function(result, item){return result+item.total},0);
    return (
     <>
     {/* Web */}
      <div className='fixed-bottom d-none d-md-block' >
        <Row>
            <Col md={{span:3, offset:9}} className='px-4'style={{backgroundColor:"#5d3138",color:"white",padding:"10px",borderRadius:"5px"}}>
                <h4>Total Harga : <strong className='float-end mr-2'>Rp {numberWithCommas (totalPay)}</strong></h4>
               <Button variant='success' block className='mb-2 mt-4 mr-2'  style={{width:"100%"}} onClick={()=>this.submitTotalPay(totalPay)} as={Link} to="/success">
                <FontAwesomeIcon icon={faShoppingBasket}/> <strong>Bayar</strong>  
               </Button>
            </Col>
            
        </Row>
        
        
      </div>
        {/* Mobile */}
      <div className='d-sm-block d-md-none' >
        <Row>
            <Col md={{span:3, offset:9}} className='px-4'style={{backgroundColor:"#5d3138",color:"white",padding:"10px",borderRadius:"5px"}}>
                <h4>Total Harga : <strong className='float-end mr-2'>Rp {numberWithCommas (totalPay)}</strong></h4>
               <Button variant='success' block className='mb-2 mt-4 mr-2'  style={{width:"100%"}} onClick={()=>this.submitTotalPay(totalPay)}>
                <FontAwesomeIcon icon={faShoppingBasket}/> <strong>Bayar</strong>  
               </Button>
            </Col>
            
        </Row>
        
        
      </div>
     </>
    )
  }
}
