import React,{Component} from "react";
import {Badge, Card, Col,ListGroup, Row} from 'react-bootstrap';
import { numberWithCommas } from "../utils/utils";
import TotalPay from "./TotalPay";
import ModalKeranjang from "./ModalKeranjang";
import swal from 'sweetalert';
import axios from "axios";
import { API_URL } from "../utils/constants";
export default class Hasil extends Component{
    constructor(props) {
      super(props)
    
      this.state = {
         showModal:false,
         keranjangDetail:false,
         jumlah:0,
         keterangan:'',
         totalHarga :0
      }
    }

    handleShow = (menuKeranjang)=>{
        this.setState({
            showModal:true,
            keranjangDetail:menuKeranjang,
            jumlah : menuKeranjang.jumlah,
            keterangan:menuKeranjang.keterangan,
            totalHarga:menuKeranjang.total

        })
    }

    handleClose = ()=>{
        this.setState({
            showModal:false

        })
    }

    tambah = () =>{
        this.setState({
            jumlah:this.state.jumlah+1,
            totalHarga : this.state.keranjangDetail.product.harga*(this.state.jumlah+1)
        })
    }

    kurang = () =>{
        if(this.state.jumlah !==1){
        this.setState({
            jumlah:this.state.jumlah-1,
            totalHarga : this.state.keranjangDetail.product.harga*(this.state.jumlah-1)
        })
    }

    }

    changeHandler = (event) => {
        this.setState({
            keterangan : event.target.value
        })
    }

    handleSubmit = (event) => {
        event.preventDefault();
        this.handleClose();
        const data = {
            jumlah: this.state.jumlah,
            total: this.state.totalHarga,
            product: this.state.keranjangDetail.product,
            keterangan : this.state.keterangan
          };
  
          axios
            .put(API_URL + "keranjangs/"+this.state.keranjangDetail.id, data)
            .then((res) => {
                this.props.getListKeranjang();
              swal({
                title: "Order Updated",
                text: "Successfully Updated " + data.product.nama,
                icon: "success",
                button: false,
                timer: 1500,
              });
            })
            .catch((error) => {
              console.log(error);
            });
      
        
    }
    hapusPesanan = (id) => {
        
        this.handleClose();
        
  
          axios
            .delete(API_URL + "keranjangs/"+id)
            .then((res) => {
              this.props.getListKeranjang();
              swal({
                title: "Order Deleted",
                text: "Successfully Deleted " + this.state.keranjangDetail.product.nama,
                icon: "error",
                button: false,
                timer: 1500,
              });
            })
            .catch((error) => {
              console.log(error);
            });
      
        
    }
    
    
    render(){
         const {keranjangs}= this.props
        return(
           
            <Col md={3} mt="2" style={{padding:'15px'}} >
                <h4><strong style={{color:'#5d3138'}}>Cart</strong></h4>
                <hr/>
                {keranjangs.length !== 0 && (
                    <Card className="overflow-auto hasil">
                <ListGroup variant="flush">
                    {keranjangs.map((menuKeranjang)=>(
                
                <ListGroup.Item key={menuKeranjang.id} onClick={()=>this.handleShow(menuKeranjang)} style={{cursor:'pointer'}}>
                    <Row>
                        <Col xs={2}>
                        <Badge bg="success">
                            {menuKeranjang.jumlah}

                        </Badge>
                        </Col>
                        <Col>
                        <h5>{menuKeranjang.product.nama}</h5>
                        <p>Rp {numberWithCommas(menuKeranjang.product.harga)}</p>
                        </Col>
                        <Col >
                        <strong className="float-right">Rp {numberWithCommas(menuKeranjang.total)}</strong>
                        </Col>
                    </Row>
                </ListGroup.Item>
                
     
                    ))}
                    <ModalKeranjang handleClose={this.handleClose}{...this.state}tambah={this.tambah}kurang={this.kurang} changeHandler={this.changeHandler}handleSubmit={this.handleSubmit}hapusPesanan={this.hapusPesanan}/>
                   
                </ListGroup>
                </Card>
                )}
                <TotalPay keranjangs={keranjangs}{...this.props}/>
            </Col>
        )
    }
}