import React from 'react'
import {Col, Card} from "react-bootstrap"
import {numberWithCommas} from '../utils/utils'
const Menus = ({menu, addKeranjang}) => {
  return (
    <Col md={4} xs={6} className='mb-4'>
    <Card style={{cursor:"pointer",height:'300px'}} className='shadow' onClick={()=>addKeranjang(menu)}>
    <Card.Img variant="top" style={{height: '200px',width:'auto'}} src={"assets/images/"+menu.category.nama.toLowerCase()+"/"+menu.gambar} />
    <Card.Body>
      <Card.Title style={{fontSize:'14px'}}>{menu.nama} <strong>{menu.kode}</strong></Card.Title>
      <Card.Text>
        Rp {numberWithCommas(menu.harga)}
      </Card.Text>
    </Card.Body>
  </Card>
    </Col>
    
  )
}

export default Menus

